<template>
	<!-- 分类页面 -->
	<view>
		<!-- 使用自定义的搜索组件 -->
		<my-search @click="gotoSearch"></my-search>

		<view class="scroll-view-container">
			<!-- 左侧的滚动视图区域 -->
			<scroll-view class="left-scroll-view" scroll-y :style="{height: wh + 'px'}">
				<block v-for="(item, index) in cateList" :key="index">
					<view :class="['left-scroll-view-item', index === active ? 'active' : '']"
						@click="activeChanged(index)">
						{{item.cate_name}}
					</view>
				</block>
			</scroll-view>
			<!-- 右侧的滚动视图区域 -->
			<scroll-view class="right-scroll-view" scroll-y :style="{height: wh + 'px'}" :scroll-top="scrollTop">
				<view class="cate-lv2" v-for="(item2, index2) in cateLevel2" :key="index2">
					<view class="cate-lv2-title">/ {{item2.cate_name}} /</view>
					<!-- 动态渲染三级分类的列表数据 -->
					<view class="cate-lv3-list">
						<!-- 三级分类 Item 项 -->
						<view class="cate-lv3-item" v-for="(item3, index3) in item2.children" :key="index3"
							@click="gotoGoodsList(item3)">
							<!-- 图片 -->
							<image :src="item3.cate_icon"></image>
							<!-- 文本 -->
							<text>{{item3.cate_name}}</text>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import {
		getCateList
	} from '@/api/cate.js'
	export default {
		data() {
			return {
				// 窗口的可用高度 = 屏幕高度 - navigationBar高度 - tabBar 高度
				wh: 0,
				// 分类数据列表
				cateList: [],
				// 当前选中项的索引，默认让第一项被选中
				active: 0,
				// 二级分类列表
				cateLevel2: [],
				// 滚动条距离顶部的距离
				scrollTop: 0
			};
		},
		onLoad() {
			// 获取当前系统的信息
			const sysInfo = uni.getSystemInfoSync()
			// 可用高度 = 屏幕高度 - navigationBar高度 - tabBar高度 - 自定义的search组件高度
			this.wh = sysInfo.windowHeight - 50
			// 调用获取分类列表数据的方法
			this.cateData()
		},
	
		methods: {
			async cateData() {
				// 发起请求
				let res = await getCateList();
				console.log("获取分类数据");
				console.log(res);
				// 转存数据
				this.cateList = res.message
				console.log("分类数据");
				console.log(this.cateList);
				// 为二级分类赋值
				this.cateLevel2 = res.message[0].children
				console.log("二级分类数据");
				console.log(this.cateLevel2);
			},
			// 选中项改变的事件处理函数
			activeChanged(index) {
				this.active = index
				// 为二级分类列表重新赋值
				this.cateLevel2 = this.cateList[index].children
				// 触发一级事件后让scrolltop的值重新为0
				// 给scrolltop设的值不能和data里的默认值一样 让 scrollTop 的值在 0 与 1 之间切换 效果几乎一样
				this.scrollTop = this.scrollTop === 0 ? 1 : 0
			},
			// 点击三级分类项跳转到商品列表页面
			gotoGoodsList(item3) {
				console.log("三级分类跳转");
				console.log(item3.cate_id);
				uni.navigateTo({
					url: '/subpkg/goods_list/goods_list?cid=' + item3.cate_id
				})
			},
			// 跳转到分包中的搜索页面
			gotoSearch() {
				uni.navigateTo({
					url: '/subpkg/search/search'
				})
			}
		}
	}
</script>

<style lang="scss">
	.scroll-view-container {
		display: flex;

		.left-scroll-view {
			width: 120px;

			.left-scroll-view-item {
				line-height: 60px;
				background-color: #ffffff50;
				text-align: center;
				font-size: 12px;
			}

			// 激活项的样式
			.active {
				background-color: #ffffff;
				position: relative;

				// 渲染激活项左侧的红色指示边线
				&::before {
					content: ' ';
					display: block;
					width: 3px;
					height: 30px;
					background-color: #c00000;
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}

		.cate-lv2-title {
			font-size: 12px;
			font-weight: bold;
			text-align: center;
			padding: 15px 0;
		}

		.cate-lv3-list {
			display: flex;
			flex-wrap: wrap;

			.cate-lv3-item {
				width: 25%;
				margin-bottom: 10px;
				display: flex;
				flex-direction: column;
				align-items: center;

				image {
					width: 60px;
					height: 60px;
				}

				text {
					font-size: 12px;
				}
			}
		}
	}
</style>
